.App {
  text-align: center;
  background-color: #181818;
  width: 100vw;
  height: 100vh;
  border: 5px solid #ded4bf;
  box-sizing: border-box;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.AppBorder{
  border: 1px solid #ded4bf;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;

}
.BackgroundImage::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://uploads-ssl.webflow.com/6340524a90d47a1821f48d2d/63d0386c50677a1ea97e2365_image.webp');
  opacity: 0.25;
    pointer-events: none; /* Добавьте это свойство */

}

.topSide{
  background-color: red;
  width: 100%;
  height: 10%;
}


.AppBody{
  width: 100%;
  height: 100%;
  padding: 3vh;
  display: flex;
    justify-content: space-between;
  box-sizing: border-box;

}

.stavnya{
  width: 20%;
  height: 100%;
  border: 1px solid #ded4bf;
  box-sizing: border-box;
  padding: 5px;


}
.window{
  height: 100%;
  border: 1px solid #ded4bf;
  padding: 5px;
  box-sizing: border-box;

}
.borderInner{
  border: 1px solid #ded4bf;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}
.contacts{
  height: 10vh;
  display: flex;
  align-items: end;
    justify-content: space-around;
    padding-bottom: 5px;
}

.imgIcons{
  width: 35x;
  height: 35px;
}
@media only screen and (max-width: 768px) {
  .imgIcons {
  width: 20;
  height: 20px;  }
}



.CenterName{
    color: #ded4bf;
    text-align: center;
    letter-spacing: 0;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 0;
    font-family: Romieligatures,Times New Roman,sans-serif;
    font-size: 10vw;
    font-weight: 400;
    line-height: 13vw;
    }
    .linkContainer{
      display: flex;
      flex-direction: column;
    }

    .linkStyle{
    color: #ded4bf;
    font-size: 1vw;
    font-weight: 400;

    }
    .download{
      color: #ded4bf;
      padding: 15px;
      border: 1px solid #ded4bf;
      text-decoration: none;
    } 


  @media only screen and (orientation: landscape) {

    .CenterName{
      font-size: 5vw;
      font-weight: 400;
      line-height: 5vw;
      }
    .download{
      color: #ded4bf;
      padding: 5px;
      border: 1px solid #ded4bf;
      text-decoration: none;
      font-size: 10px;
    } 

  }



